.myjumbo {
  background-image: url(./background.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  color: #efefef;
  height: 580px;
  position: relative;
  display: flex;
  justify-content: left;
  align-items: left;
  padding-top: 0px;
  flex-direction: column;
}
.hi {
  background-image: url(./background.jpg);
  background-size: cover;
}

.btnshop {
  text-decoration: none;
  border: 3px solid white;
  background: none;

  padding: 4px;

  color: white;
  font-family: "Raleway";
  font-size: 21px;
  cursor: pointer;
  transition: 0.8s;

  overflow: hidden;
  position: absolute;

  justify-content: space-around;
  align-items: center;
}
.btnshop:hover {
  color: black;
  z-index: 1;
  border: 3px solid black;
}
.btnshop::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: white;
  z-index: -1;
  transition: 0.8s;
  top: 0;
  border-radius: 0 0 50% 50%;
}
.btnshop:hover::after {
  height: 180%;
}
.jumboShop {
  background-image: url(./shopsnow.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  color: black;
  height: 480px;
  font-family: "Raleway";
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.shopwords {
  color: black;

  font-family: "Raleway";

  display: flex;
  justify-content: center;
  align-items: center;
}

.jumboAbout {
  background-image: url(./bridge.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  color: #efefef;
  height: 480px;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Raleway";
}

.jumboWords {
  color: black;

  font-family: "Raleway";

  display: flex;
  justify-content: center;
  align-items: center;
}
.words {
  display: flex;
  justify-content: space-around;
  align-items: center;

  vertical-align: middle;
  padding-top: 0px;
}
.footerinsta {
  color: white;
  margin: 0 1rem;
  display: inline-block;
  transition: transform 250ms;
  font-size: 24px;
  text-align: center;
  align-items: center;
}
.footerinsta:hover {
  color: grey;
}
.bodyCard {
  padding: 30px;
  margin: 0px;
  width: 600px;

  border: 3px solid #eee;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: white;
  justify-content: space-around;
  flex-wrap: wrap;
}
.bodyCard img {
  width: 100%;
  padding: 0px;
}
.bodyCard p {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 17px;
  letter-spacing: 1px;
}
.bodyCard h2 {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 35px;
  letter-spacing: 1px;
}
.bodyCard h3 {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 25px;
  letter-spacing: 1px;
}
.buy {
  height: 40px;
  width: 90%;
  margin: 0px 5% 20px;
  border: none;
  box-shadow: 0px 5px 9px rgba(0, 0, 0, 0.3);
}
.cards {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: auto auto auto;
  padding: 30px;
  box-sizing: border-box;
  grid-gap: 30px;
}
.navbar {
  background-color: #fafafa;
}
.productDescription {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.Announcement {
  background-color: #db5c50;
  height: 27px;
  vertical-align: bottom;

  padding-bottom: 0px;
  padding-top: 5px;
}
.drop {
  font-size: 14px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.maintext {
  opacity: 100;
  margin-top: 0px;
  font-size: 60px;
  text-align: left;
  overflow: hidden;
  animation: maintexta 6s;
  padding-top: 30vh;
  animation-fill-mode: forwards;
}

@keyframes maintexta {
  from {
    padding-left: 0vw;
  }
  to {
    padding-left: 7vw;
  }
}
.btnbox {
  padding-top: 12vh;
  overflow: hidden;
  display: block;
  animation: mainbtna 2s;
  animation-fill-mode: forwards;
}
@keyframes mainbtna {
  from {
    padding-left: 100vw;
  }
  to {
    padding-left: 39vw;
  }
}
